import React, { Component } from "react";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import Stepper from "react-stepper-horizontal";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import Axios from "axios";
import SelectSearch from "react-select-search";
import {
  getValidasiCustomerPembayaranPerbon,
  getValidasiCustomerPembelianLM,
  getValidasiCustomerPenjualan,
  getValidasiCustomerReturnPenjualan,
  getValidasiCustomerTitipGrCustomer,
  getValidasiCustomerTolakPembayaranCustomer,
  getValidasiCustomerPembayaranLM,
  getValidasiCustomerTolakPembayaranReturn,
} from "../actions/validasiCustomerAction";
// import { addFaktur, checkReplikasiValidasi, deleteFaktur } from "../FindIndex";
import { berhasil, gagal } from "../Alert";
import { getDataSelectedValidasiSupplier } from "../actions/validasiSupplierAction";
import HeaderCard from "../HeaderCard";
import { AxiosMasterPut } from "../Axios";

const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);

const maptoState = (state) => {
  return {
    listValidasiCustomer: state.provinsi.listValidasiCustomer,
    listDataSelectedValidasiSupplier:
      state.provinsi.listDataSelectedValidasiSupplier,
  };
};
const server = process.env.REACT_APP_BACKEND_URL;
const serverNonMaster = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
class validasiCustomer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
        },
        {
          dataField: "no_bon",
          text: "Nomor Bon",
        },
        {
          dataField: "no_trx",
          text: "Nomor Transaksi",
        },
        {
          dataField: "kode_customer",
          text: "Nama Aktor",
        },
        {
          dataField: "total_netto",
          text: "Murni(Gr)",
          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "total_bruto",
          text: "Bruto(Gr)",
        },
        {
          dataField: "bon_rp",
          text: "Bon (RP)",
        },
        {
          dataField: "titip_gr",
          text: "Titip (Gr)",
        },
        {
          dataField: "titip_rp",
          text: "Titip (Rp)",
        },
      ],
      data: [
        {
          tanggal: "23/11/2020",
        },
      ],
      step: 0,
      step1: "row",
      step2: "row d-none",
      step3: "row d-none",
      listSales: [],
      listCustomer: [],
    };
  }
  updateButton(data) {
    switch (data) {
      case "PENJUALAN":
        this.props.dispatch(getValidasiCustomerPenjualan());
        break;
      case "RETURN PENJUALAN":
        this.props.dispatch(getValidasiCustomerReturnPenjualan());
        break;
      case "PEMBAYARAN PER BON":
        this.props.dispatch(getValidasiCustomerPembayaranPerbon());
        break;
      case "TOLAKAN PEMBAYARAN CUSTOMER":
        this.props.dispatch(getValidasiCustomerTolakPembayaranCustomer());
        break;
      case "TITIP GR CUSTOMER":
        this.props.dispatch(getValidasiCustomerTitipGrCustomer());
        break;
      case "PENJUALAN LM":
        this.props.dispatch(getValidasiCustomerPembayaranLM());
        break;
      case "PEMBELIAN LM":
        this.props.dispatch(getValidasiCustomerPembelianLM());
        break;
      default:
        console.log("ERROR");
        break;
    }
  }
  kirimvalidasi(data) {
    console.log("BERHASIL");
    var raw = JSON.parse(localStorage.getItem("FakturTerpilih_kirim")) || [];
    var hasil = {
      trx_customer: raw,
    };
    switch (data) {
      case "PENJUALAN":
        AxiosMasterPut("validate-customer/validate", hasil)
          .then(() => berhasil("Berhasil Validasi"))
          .then(() => this.prevStep())
          .then(() => this.props.dispatch(getValidasiCustomerPenjualan()))
          .then(() => localStorage.removeItem("FakturTerpilih"))
          .then(() => localStorage.removeItem("FakturTerpilih_kirim"))
          .catch((err) =>
            gagal(
              `Gagal Validasi, Check Koneksi Anda, Detial : ${err.response.data}`
            )
          );
        break;
      case "RETURN PENJUALAN":
        Axios.put(serverNonMaster + "cst/dc/rtr", hasil)
          .then(() => berhasil("Berhasil Validasi"))
          .then(() => this.prevStep())
          .then(() => this.props.dispatch(getValidasiCustomerReturnPenjualan()))
          .then(() => localStorage.removeItem("FakturTerpilih"))
          .catch(() => gagal("Gagal Validasi, Check Koneksi Anda"));
        break;
      case "PEMBAYARAN PER BON":
        AxiosMasterPut("validate-customer/validate", hasil)
          .then(() => berhasil("Berhasil Validasi"))
          .then(() => this.prevStep())
          .then(() =>
            this.props.dispatch(getValidasiCustomerPembayaranPerbon())
          )
          .then(() => localStorage.removeItem("FakturTerpilih"))
          .catch(() => gagal("Gagal Validasi, Check Koneksi Anda"));
        break;
      case "TOLAKAN PEMBAYARAN CUSTOMER":
        Axios.put(serverNonMaster + "cst/dc/tlkbyr", hasil)
          .then(() => berhasil("Berhasil Validasi"))
          .then(() => this.prevStep())
          .then(() =>
            this.props.dispatch(getValidasiCustomerTolakPembayaranCustomer())
          )
          .then(() => localStorage.removeItem("FakturTerpilih"))
          .catch(() => gagal("Gagal Validasi, Check Koneksi Anda"));
        break;
      case "TITIP GR CUSTOMER":
        Axios.put(serverNonMaster + "cst/dc/ttp", hasil)
          .then(() => berhasil("Berhasil Validasi"))
          .then(() => this.prevStep())
          .then(() => this.props.dispatch(getValidasiCustomerTitipGrCustomer()))
          .then(() => localStorage.removeItem("FakturTerpilih"))
          .catch(() => gagal("Gagal Validasi, Check Koneksi Anda"));
        break;
      case "PENJUALAN LM":
        Axios.put(serverNonMaster + "cst/dc/lm", hasil)
          .then(() => berhasil("Berhasil Validasi"))
          .then(() => this.prevStep())
          .then(() => this.props.dispatch(getValidasiCustomerPembayaranLM()))
          .then(() => localStorage.removeItem("FakturTerpilih"))
          .catch(() => gagal("Gagal Validasi, Check Koneksi Anda"));
        break;
      case "PEMBELIAN LM":
        Axios.put(serverNonMaster + "cst/dc/lm", hasil)
          .then(() => berhasil("Berhasil Validasi"))
          .then(() => this.prevStep())
          .then(() => this.props.dispatch(getValidasiCustomerPembelianLM()))
          .then(() => localStorage.removeItem("FakturTerpilih"))
          .catch(() => gagal("Gagal Validasi, Check Koneksi Anda"));
        break;
      default:
        console.log("ERROR");
        break;
    }
  }
  disabledButton = (e) => {
    e.target.disabled = true;
  };
  nextStep = () => {
    const { step } = this.state;
    if (step === 0) {
      this.setState({ step: step + 1, step2: "row", step1: "row d-none" });
    }
    this.props.dispatch(getDataSelectedValidasiSupplier());
  };
  prevStep = () => {
    const { step } = this.state;
    if (step === 1) {
      this.setState({ step: step - 1, step2: "row d-none", step1: "row" });
    }
  };
  changeTransaksi = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
    switch (e.target.value) {
      case "PENJUALAN":
        this.props.dispatch(getValidasiCustomerPenjualan());
        break;
      case "RETURN PENJUALAN":
        this.props.dispatch(getValidasiCustomerReturnPenjualan());
        break;
      case "PEMBAYARAN PER BON":
        this.props.dispatch(getValidasiCustomerPembayaranPerbon());
        break;
      case "TOLAKAN PEMBAYARAN CUSTOMER":
        this.props.dispatch(getValidasiCustomerTolakPembayaranCustomer());
        break;
      case "TOLAKAN RETURN CUSTOMER":
        this.props.dispatch(getValidasiCustomerTolakPembayaranReturn());
        break;
      case "TITIP GR CUSTOMER":
        this.props.dispatch(getValidasiCustomerTitipGrCustomer());
        break;
      case "PENJUALAN LM":
        this.props.dispatch(getValidasiCustomerPembayaranLM());
        break;
      case "PEMBELIAN LM":
        this.props.dispatch(getValidasiCustomerPembelianLM());
        break;
      default:
        console.log("ERROR");
        break;
    }
  };

  componentDidMount() {
    localStorage.setItem("FakturTerpilih_kirim", "[]");
    localStorage.setItem("FakturTerpilih", "[]");
    Axios.get(server + "storages2?tipeData=SALES").then((res) =>
      this.setState({
        listSales: res.data,
      })
    );
    Axios.get(server + "customers").then((res) =>
      this.setState({
        listCustomer: res.data,
      })
    );
  }
  // ONCHANGE PILIH KOTA
  onSubmitToko(data) {
    this.props.change("namaToko", data);
  }
  render() {
    var namaToko = this.state.listCustomer.map((obj) => {
      var payload = {
        value: obj.kodeCustomer,
        name: obj.namaToko,
      };
      return payload;
    });
    var sales = this.state.listSales.map((obj) => {
      var payload = {
        value: obj.kodeLokasi,
        name: obj.namaLokasi,
      };
      return payload;
    });
    const selectRow = {
      mode: "checkbox",
      clickToSelect: true,
      onSelect: (row, isSelect, rowIndex, e) => {
        console.log(row.noFaktur);
        let array = JSON.parse(localStorage.getItem("FakturTerpilih")) || [];
        let array2 =
          JSON.parse(localStorage.getItem("FakturTerpilih_kirim")) || [];
        const data = {
          tanggal: row.tanggal,
          no_bon: row.no_bon,
          no_trx: row.no_trx,
          kode_customer: row.kode_customer,
          total_bruto: row.total_bruto,
          total_netto: row.total_netto,
          bon_rp: row.bon_rp,
          titip_gr: row.titip_gr,
          titip_rp: row.titip_rp,
        };
        const kirim = {
          no_trx: row.no_trx,
          keterangan: row.keterangan,
        };
        if (isSelect) {
          var index1 = array.findIndex((item, i) => {
            return item.no_bon === row.no_bon;
          });
          var index2 = array2.findIndex((item, i) => {
            return item.no_bon === row.no_bon;
          });
          if (index1 < 0) {
            array.push(data);
            array2.push(kirim);
          } else {
            array.splice(index1, 1);
            array2.splice(index2, 1);
          }
          localStorage.setItem("FakturTerpilih", JSON.stringify(array));
          localStorage.setItem("FakturTerpilih_kirim", JSON.stringify(array2));
        } else {
          var index = array.findIndex((item, i) => {
            return item.no_bon === row.no_bon;
          });
          var index3 = array.findIndex((item, i) => {
            return item.no_bon === row.no_bon;
          });
          array.splice(index, 1);
          array2.splice(index3, 1);
          localStorage.setItem("FakturTerpilih", JSON.stringify(array));
          localStorage.setItem("FakturTerpilih_kirim", JSON.stringify(array));
        }
      },
      onSelectAll: (isSelect, rows, e) => {
        var array = [];
        var array2 = [];
        rows.forEach(function (list) {
          const data = {
            tanggal: list.tanggal,
            no_bon: list.no_bon,
            no_trx: list.no_trx,
            kode_customer: list.kode_customer,
            total_bruto: list.total_bruto,
            total_netto: list.total_netto,
            bon_rp: list.bon_rp,
            titip_gr: list.titip_gr,
            titip_rp: list.titip_rp,
          };
          const kirim = {
            no_trx: list.no_trx,
            keterangan: list.keterangan,
          };
          array.push(data);
          array2.push(kirim);
        });
        if (isSelect) {
          localStorage.setItem("FakturTerpilih", JSON.stringify(array));
          localStorage.setItem("FakturTerpilih_kirim", JSON.stringify(array2));
        } else {
          localStorage.removeItem("FakturTerpilih");
          localStorage.removeItem("FakturTerpilih_kirim");
        }
      },
    };
    return (
      <div className="container-fluid">
        <div className="card">
          <HeaderCard title="Validasi Customer" />
          <div className="card-body">
            <div className="col-lg-12">
              <div className="row">
                <div className="col-lg-12 col-md-12">
                  <Stepper
                    steps={[
                      { title: "Pilih Data" },
                      { title: "Validasi Data" },
                      // { title: "Data Rekening" },
                    ]}
                    activeStep={this.state.step}
                  />
                </div>
                <div className="col-lg-12">
                  <div className={this.state.step1}>
                    <div className="col-lg-3 mb-3">
                      <label htmlFor="">Jenis Transaksi</label>
                      <Field
                        name="tipeTransaksi"
                        label="Dari"
                        component="select"
                        className="form-control"
                        onChange={this.changeTransaksi("tipeTransaksi")}
                      >
                        <option value="DEFAULT" key="DEFAULT">
                          SILAHKAN PILIH
                        </option>
                        <option value="PENJUALAN" key="PENJUALAN">
                          PENJUALAN
                        </option>
                        <option value="RETURN PENJUALAN" key="RETURN PENJUALAN">
                          RETURN PENJUALAN
                        </option>
                        <option
                          value="PEMBAYARAN PER BON"
                          key="PEMBAYARAN PER BON"
                        >
                          PEMBAYARAN PER BON
                        </option>
                        <option
                          value="TOLAKAN PEMBAYARAN CUSTOMER"
                          key="TOLAKAN PEMBAYARAN CUSTOMER"
                        >
                          TOLAKAN PEMBAYARAN CUSTOMER
                        </option>

                        <option
                          value="TITIP GR CUSTOMER"
                          key="TITIP GR CUSTOMER"
                        >
                          TITIP GR CUSTOMER
                        </option>
                        <option value="PENJUALAN LM" key="PENJUALAN LM">
                          PENJUALAN LM
                        </option>
                        <option value="PEMBELIAN LM" key="PEMBELIAN LM">
                          PEMBELIAN LM
                        </option>
                      </Field>
                    </div>
                    <div className="col-lg-9"></div>
                    <div className="col-lg-3">
                      <label htmlFor="">Nama Sales</label>
                      <SelectSearch
                        options={sales}
                        search
                        placeholder="Silahkan Pilih"
                        onChange={(data) => this.props.change("sales", data)}
                      />
                    </div>
                    <div className="col-lg-3 d-none">
                      <Field
                        name="sales"
                        label="Nama Toko"
                        type="text"
                        component={renderField}
                      ></Field>
                    </div>
                    <div className="col-lg-3">
                      <label htmlFor="">Nama Toko</label>
                      <SelectSearch
                        options={namaToko}
                        search
                        placeholder="Silahkan Pilih"
                        onChange={(data) => this.props.change("namaToko", data)}
                      />
                    </div>
                    <div className="col-lg-3 d-none">
                      <Field
                        name="namaToko"
                        label="Nama Toko"
                        type="text"
                        component={renderField}
                      ></Field>
                    </div>
                    <div className="col-lg-3">
                      <Field
                        name="tanggal"
                        label="Tanggal Transaksi"
                        component={renderField}
                        type="date"
                      />
                    </div>
                    <div className="col-lg-3">
                      <label htmlFor="">Tipe Transaksi</label>
                      <Field
                        name="Tipe Transaksi"
                        label="Tanggal Transaksi"
                        component="select"
                        className="form-control"
                      >
                        <option
                          value="
                            DEFAULT"
                          key="
                            DEFAULT"
                        >
                          Silahkan PIlih
                        </option>
                        <option value="UMUM" key="UMUM">
                          UMUM
                        </option>
                        <option value="FICO" key="FICO">
                          FICO
                        </option>
                      </Field>
                    </div>
                    <div className="col-lg-12">
                      <div className="text-right">
                        <button className="btn btn-primary">
                          <i className="fa fa-eye"></i> Lihat Data
                        </button>
                      </div>
                    </div>
                    <div className="col-lg-12 mt-3">
                      {this.props.listValidasiCustomer ? (
                        <ToolkitProvider
                          keyField="no_bon"
                          data={this.props.listValidasiCustomer}
                          columns={this.state.columns}
                          search
                        >
                          {(props) => (
                            <div className="form-group">
                              <BootstrapTable
                                {...props.baseProps}
                                striped
                                hover
                                condensed
                                bordered={true}
                                headerClasses="header-table"
                                pagination={paginationFactory()}
                                selectRow={selectRow}
                              />
                            </div>
                          )}
                        </ToolkitProvider>
                      ) : (
                        <BootstrapTable
                          keyField="noFaktur"
                          data={[]}
                          columns={this.state.columns}
                          bordered={true}
                          headerClasses="header-table"
                          noDataIndication="Tidak Ada Data"
                        />
                      )}
                    </div>
                    <div className="col-lg-12">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          onClick={() => this.nextStep()}
                        >
                          Next
                          <i className="fa fa-chevron-right ml-3"></i>
                        </button>
                      </div>
                    </div>
                  </div>
                  <div className={this.state.step2}>
                    <div className="col-lg-12 mt-3">
                      {this.props.listDataSelectedValidasiSupplier ? (
                        <ToolkitProvider
                          keyField="noFaktur"
                          data={this.props.listDataSelectedValidasiSupplier}
                          columns={this.state.columns}
                          search
                        >
                          {(props) => (
                            <div className="form-group">
                              <BootstrapTable
                                {...props.baseProps}
                                striped
                                hover
                                condensed
                                bordered={true}
                                headerClasses="header-table"
                                pagination={paginationFactory()}
                              />
                            </div>
                          )}
                        </ToolkitProvider>
                      ) : (
                        <BootstrapTable
                          keyField="noFaktur"
                          data={[]}
                          columns={this.state.columns}
                          bordered={true}
                          headerClasses="header-table"
                          noDataIndication="Tidak Ada Data"
                        />
                      )}
                    </div>
                    <div className="col-lg-12">
                      <div className="row">
                        <div className="col-lg-6">
                          <div className="text-left">
                            <button
                              className="btn btn-primary"
                              onClick={() => this.prevStep()}
                            >
                              <i className="fa fa-chevron-left mr-3"></i>
                              Kembali
                            </button>
                          </div>
                        </div>
                        <div className="col-lg-6">
                          <div className="text-right">
                            <button
                              className="btn btn-primary"
                              onClick={() =>
                                this.kirimvalidasi(this.state.tipeTransaksi)
                              }
                            >
                              <i className="fa fa-check mr-3"></i>
                              VALIDASI
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

validasiCustomer = reduxForm({
  form: "validasiCustomer",
  enableReinitialize: true,
})(validasiCustomer);
export default connect(maptoState, null)(validasiCustomer);
